.c-bgctrl {
  background-color: rgba($black, .6);
  border: 1px solid rgba($grey-darkest, .6);
  border-radius: 3px;
  color: $white;
  max-width: 50rem;
  padding: .5rem;
  position: relative;
  width: 100%;

  &__errors {
    .c-validation__item { margin-top: 1.2rem; }
  }

  &__file,
  &__url {
    background-color: rgba($white, .1);
    border: 1px solid rgba($white, .2);
    color: $white;
    font-size: 1.3rem;
    height: 4rem;
    line-height: 2rem;
    margin-top: 1.2rem;
    padding: .9rem 1.4rem;
  }

  &__filename {
    display: none;
    font-size: 1.1rem;
    font-weight: 400;
    margin-top: 1rem;

    &:not(:empty) { display: block; }
  }

  &__label {
    border-radius: 3px;
    box-shadow: inset 0 0 0 1px rgba($white, .1);
    display: block;
    font-weight: bold;
    margin-bottom: .5rem;
    padding: 1.5rem;
    text-align: center;
    z-index: 0;

    &.checked { box-shadow: inset 0 0 0 2px $blue-med; }
  }

  &__note {
    display: block;
    font-size: .8em;
    font-style: normal;
    font-weight: 400;
    opacity: .66;
  }

  &__select[type='radio'] { display: none; }

  &__title {
    border-bottom: 1px solid rgba($white, .2);
    color: $white;
    font-size: 1.1rem;
    line-height: 2.8rem;
    margin-bottom: .5rem;
    padding-bottom: .5rem;
    text-align: center;
  }

  &__txt { cursor: pointer; }

  &__option {
    &-default {
      border-top: 1px solid rgba($white, .2);
      font-size: 1.1rem;
      line-height: 3.4rem;
      text-align: center;
      text-decoration: underline;

      input { display: none; }

      .c-bgctrl {
        &__label {
          background-color: transparent;
          box-shadow: none;
          margin-bottom: 0;
          padding: 0;
          width: 100%;

          &::after,
          &::before { display: none; }
        }

        &__txt:hover { color: $white; }
      }
    }
  }

  .c-upload {
    &.has-preview {
      background-color: rgba($white, .08);
      border: 1px dashed rgba($white, .84);
      border-radius: 3px;
      color: $white;
      font-weight: 400;
      margin-top: 1rem;
      text-align: center;

      .c-upload__info { justify-items: center; }
    }

    &__action,
    &__change {
      background-color: rgba($white, .24);
      box-shadow: inset 0 -2px 0 rgba($white, .24);
      color: $white;
    }

    .c-upload__preview {
      display: none;

      &-spinner {
        border-left-color: rgba($white, .66);
        border-top-color: rgba($white, .66);

        + .c-upload__info {
          justify-items: start;
          text-align: left;
        }
      }
    }
  }
}
