.m-markdown {
  display: flex;

  &__guide {
    flex: 1;
    margin: -2rem;
    max-width: 60rem;
    overflow: auto;
    padding: 3.5rem 4rem;

    &::-webkit-scrollbar-track { margin: 4rem 0; }

    code,
    pre {
      background-color: rgba($black, .08);
      border-radius: 3px;
      display: inline-block;
      padding: 0 .2rem;
    }

    code { font-family: $mono-family; }

    dl,
    h1,
    h2,
    h3,
    p {
      margin-bottom: 1.5rem;
    }

    dl {
      display: grid;
      grid-gap: 1rem;
      grid-template-columns: auto 1fr;
    }

    dt { font-weight: 700; }

    h2 {
      font-size: 1.8rem;
      text-transform: none;
    }

    h3 { font-size: 1em; }

    pre {
      border: 1px solid rgba($black, .12);
      display: block;
      margin-bottom: 2rem;
      padding: .5rem 1rem;

      code {
        background-color: transparent;
        display: block;
        padding: 0;
      }
    }
  }
}
